<template>
    <div class="card-page">
        <div class="page-head">
            <a class="l">返回</a>
            <a>我的福卡</a>
            <a class="r">福卡榜</a>
        </div>
        <div class="page-title">
            <h2>集齐五福，拼手气分5亿</h2>
            <p>69,897,865人已集齐，2月4号22:18开奖</p>
        </div>
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(item, index) in list" :key="index">
                    <div class="main-card">
                        <transition name="filid">
                            <div class="card-front" v-if="!item.showAfter" key="front">
                                <div class="c-image"><img src="../assets/images/ggg.jpg"></div>
                                <div class="c-body">
                                    <a class="fff">敬业福</a>
                                    <p class="c-text">支付宝祝你新年快乐</p>
                                    <div class="c-fuzi"><img src="../assets/images/fu.jpg"></div>
                                    <a class="sz">2/6</a><a class="fz" @click="showCardafter(index)">翻转</a>
                                </div>
                                <div class="c-p-logo"><i></i></div>
                            </div>
                        </transition>
                        <transition name="filid">
                            <div class="card-after" v-if="item.showAfter" key="after">
                                <div class="c-image"><img src="../assets/images/ggg.jpg"></div>
                                <div class="c-body">
                                    <a class="sz">2/6</a><a class="fz" @click="showCardafter(index)">翻转</a>
                                </div>
                            </div>
                        </transition>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import 'swiper/dist/css/swiper.min.css'
import Swiper from 'swiper/dist/js/swiper.min.js'
export default {
    data () {
        return {
            mySwiper: null,
            list: [{
                showAfter: false
            }, {
                showAfter: true
            }, {
                showAfter: false
            }]
        }
    },
    methods: {
        showCardafter (index) {
            this.list[index].showAfter = !this.list[index].showAfter
        }
    },
    mounted () {
        this.mySwiper = new Swiper('.swiper-container', {
            slidesPerView: 'auto',
            centeredSlides: true,
            watchSlidesProgress: true,
            paginationClickable: true,
            onProgress: function (a) {
                for (var i = 0; i < a.slides.length; i++) {
                    var c = a.slides[i]
                    var d = c.progress
                    var es = c.style
                    es.opacity = 1 - Math.min(Math.abs(d / 1.2), 1)
                    es.webkitTransform = es.MsTransform = es.msTransform = es.MozTransform = es.OTransform = es.transform = `translate3d(0px, 0, ${-Math.abs(150 * d)}px)`
                }
            },
            onSetTransition: function (a, b) {
                for (var c = 0; c < a.slides.length; c++) {
                    var es = a.slides[c].style
                    es.webkitTransitionDuration = es.MsTransitionDuration = es.msTransitionDuration = es.MozTransitionDuration = es.OTransitionDuration = es.transitionDuration = b + 'ms'
                }
            }
        })
    }
}
</script>

